'use client'

import { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
// import { useRouter, useSearchParams } from 'next/navigation'
import FlowBackdrop from '../components/FlowBackdrop'
import { EMAIL_RESEND_TIMEOUT_MINUTES } from '../flags'
export default function VerifyOTPPage() {
  const navigate = useNavigate()
  // const searchParams ;
  const emailParam = /*searchParams?.get('email') ||*/ ''
  
  // All useState hooks together at the top
  const [email, setEmail] = useState('')
  const [otp, setOtp] = useState(['', '', '', '', '', ''])
  const [loading, setLoading] = useState(false)
  const [resending, setResending] = useState(false)
  const [error, setError] = useState('')
  const [resendSuccess, setResendSuccess] = useState(false)
  const [resendCooldown, setResendCooldown] = useState(0)
  
  // Get email from URL param or sessionStorage
  useEffect(() => {
    const storedEmail = sessionStorage.getItem('loginEmail')
    const finalEmail = emailParam || storedEmail
    
    if (!finalEmail) {
      navigate('/login')
      return
    }
    
    setEmail(finalEmail)
  }, [emailParam, navigate])
  
  // Cooldown timer effect
  useEffect(() => {
    if (resendCooldown > 0) {
      const timer = setTimeout(() => setResendCooldown(resendCooldown - 1), 1000)
      return () => clearTimeout(timer)
    }
  }, [resendCooldown])
  
  const handleResendOTP = async () => {
    if (resendCooldown > 0 || !email) return
    
    setResending(true)
    setError('')
    setResendSuccess(false)
    
    try {
      const res = await fetch('/api/auth/send-otp', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email }),
      })
      
      const data = await res.json()
      
      if (!res.ok) {
        setError(data.error || 'Failed to resend code')
        return
      }
      if(!data.isSent){
        setError(data.issue || 'An unkown error has occured')
      }
      const resendTimeout=EMAIL_RESEND_TIMEOUT_MINUTES*60;
      setResendSuccess(true)
      setResendCooldown(resendTimeout)
      setOtp(['', '', '', '', '', ''])
      
      setTimeout(() => setResendSuccess(false), 3000)
    } catch (err: any) {
      setError(err.message || 'Failed to resend code')
    } finally {
      setResending(false)
    }
  }
  
  const handleOtpChange = (index: number, value: string) => {
    if (value.length > 1) value = value[0]
    if (!/^[0-9]*$/.test(value)) return
    
    const newOtp = [...otp]
    newOtp[index] = value
    setOtp(newOtp)
    
    // Auto-focus next input
    if (value && index < 5) {
      const nextInput = document.getElementById(`otp-${index + 1}`)
      nextInput?.focus()
    }
  }
  
  const handleKeyDown = (index: number, e: React.KeyboardEvent) => {
    if (e.key === 'Backspace' && !otp[index] && index > 0) {
      const prevInput = document.getElementById(`otp-${index - 1}`)
      prevInput?.focus()
    }
  }
  
  const handlePaste = (e: React.ClipboardEvent) => {
    e.preventDefault()
    const pastedData = e.clipboardData.getData('text/plain').slice(0, 6)
    if (!/^[0-9]+$/.test(pastedData)) return
    
    const newOtp = pastedData.split('').concat(Array(6 - pastedData.length).fill(''))
    setOtp(newOtp.slice(0, 6))
    
    // Focus last filled input
    const lastIndex = Math.min(pastedData.length, 5)
    document.getElementById(`otp-${lastIndex}`)?.focus()
  }
  
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    const otpCode = otp.join('')
    
    if (otpCode.length !== 6) {
      setError('Please enter a 6-digit OTP')
      return
    }
    
    setLoading(true)
    setError('')
    
    try {
      // Call verify endpoint
      const res = await fetch('/api/auth/verify-otp', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, otp: otpCode }),
      })
      
      const data = await res.json()
      
      if (!res.ok) {
        setError(data.error || 'Invalid OTP')
        setLoading(false)
        return
      }
      if(!data.isAuthenticated){
        setError(data.issue || 'An unknown error has occured')
        setLoading(false)
        return
      }
      
      // Sign in with custom token
      // await signInWithCustomToken(auth!, data.customToken)
      
      // Redirect to portal
      window.location.href='/portal'
    } catch (err: any) {
      setError(err.message || 'Something went wrong')
      setLoading(false)
    }
  }
  
  return (
    <div 
      className="relative min-h-screen flex flex-col"
      style={{ background: 'var(--brand-black)' }}
    >
      <FlowBackdrop />
      
      <div className="flex-1 flex items-center justify-center px-4 py-12 pt-8 min-h-screen">
        <div className="relative z-10 max-w-2xl w-full">
          {/* Header */}
          <div className="text-center mb-12">
            {/* Brand Name */}
            <h1 
              className="text-5xl sm:text-6xl md:text-7xl font-manrope font-extrabold leading-tight tracking-tight lowercase mb-8"
            >
              <span style={{ color: '#F7F7F7' }}>do </span>
              <span style={{ color: '#696969' }}>drones </span>
              <span style={{ color: '#007CCA' }}>2025</span>
              <span style={{ color: '#F7F7F7' }}>.</span>
            </h1>
            
            {/* Tagline */}
            <h2 
              className="text-3xl md:text-4xl font-manrope font-bold mb-4 leading-tight"
              style={{ color: 'var(--brand-white)' }}
            >
              verify <span className="font-besley italic font-semibold" style={{ color: 'var(--brand-primary)' }}>code</span>
            </h2>
            
            {/* Description */}
            <p 
              className="text-base md:text-lg font-manrope mb-3"
              style={{ color: 'rgba(255,255,255,0.8)' }}
            >
              Enter the 6-digit code sent to
            </p>
            <p 
              className="text-base font-manrope font-semibold"
              style={{ color: 'var(--brand-primary)' }}
            >
              {email}
            </p>
          </div>
          
          {/* Form Container */}
          <div className="max-w-xl mx-auto w-full px-4 lg:px-0">
            <div 
              className="p-8 md:p-10 rounded-3xl shadow-2xl border"
              style={{ 
                background: 'rgba(255,255,255,0.03)',
                backdropFilter: 'blur(20px)',
                borderColor: 'rgba(255,255,255,0.1)',
                boxShadow: '0 25px 50px -12px rgba(0,0,0,0.5)'
              }}
            >
              <form onSubmit={handleSubmit} className="space-y-6">
                {/* OTP Input */}
                <div className="flex justify-center gap-2 md:gap-3">
                  {otp.map((digit, index) => (
                    <input
                      key={index}
                      id={`otp-${index}`}
                      type="text"
                      inputMode="numeric"
                      maxLength={1}
                      value={digit}
                      onChange={(e) => handleOtpChange(index, e.target.value)}
                      onKeyDown={(e) => handleKeyDown(index, e)}
                      onPaste={index === 0 ? handlePaste : undefined}
                      className="w-12 h-14 md:w-14 md:h-16 text-center text-2xl font-bold rounded-xl font-manrope focus:outline-none focus:ring-2 transition"
                      style={{
                        background: 'rgba(255,255,255,0.05)',
                        border: digit ? '2px solid var(--brand-primary)' : '2px solid rgba(255,255,255,0.1)',
                        color: 'var(--brand-white)',
                        caretColor: 'var(--brand-primary)',
                      }}
                    />
                  ))}
                </div>
                
                {error && (
                  <div 
                    className="p-4 rounded-xl text-center font-manrope text-sm animate-shake"
                    style={{
                      background: 'rgba(208,65,25,0.15)',
                      border: '2px solid rgba(208,65,25,0.5)',
                      color: 'var(--brand-white)'
                    }}
                  >
                    {error}
                  </div>
                )}
                
                {resendSuccess && (
                  <div 
                    className="p-4 rounded-xl text-center font-manrope text-sm"
                    style={{
                      background: 'rgba(0,124,202,0.15)',
                      border: '2px solid rgba(0,124,202,0.5)',
                      color: 'var(--brand-primary)'
                    }}
                  >
                     New code sent! Check your email
                  </div>
                )}
                
                <button
                  type="submit"
                  disabled={loading || otp.join('').length !== 6}
                  className="w-full py-4 rounded-xl font-manrope font-semibold text-base transition-all disabled:opacity-50 disabled:cursor-not-allowed hover:scale-[1.02] active:scale-[0.98]"
                  style={{
                    background: loading 
                      ? 'rgba(0,124,202,0.5)' 
                      : 'linear-gradient(135deg, var(--brand-primary) 0%, #0096E6 100%)',
                    color: 'var(--brand-white)',
                    boxShadow: loading ? 'none' : '0 10px 30px rgba(0,124,202,.4)'
                  }}
                >
                  {loading ? (
                    <span className="flex items-center justify-center gap-3">
                      <div className="animate-spin w-5 h-5 border-2 border-white/30 border-t-white rounded-full"></div>
                      Verifying...
                    </span>
                  ) : (
                    'Verify & Continue'
                  )}
                </button>
                
                {/* Resend OTP Button */}
                <div className="text-center space-y-2 pt-2">
                  <p className="text-sm font-manrope" style={{ color: 'rgba(255,255,255,0.5)' }}>
                    Didn't receive the code?
                  </p>
                  <button
                    type="button"
                    onClick={handleResendOTP}
                    disabled={resending || resendCooldown > 0}
                    className="text-sm font-manrope font-semibold hover:opacity-80 transition disabled:opacity-50 disabled:cursor-not-allowed"
                    style={{ color: 'var(--brand-primary)' }}
                  >
                    {resending ? 'Sending...' : resendCooldown > 0 ? `Resend in ${resendCooldown}s` : 'Resend Code'}
                  </button>
                </div>
              </form>
            </div>
          </div>
          
          {/* Helper text */}
          <div className="mt-6 space-y-3">
            <div 
              className="p-4 rounded-xl border max-w-xl mx-auto"
              style={{
                background: 'rgba(250, 204, 21, 0.1)',
                borderColor: 'rgba(250, 204, 21, 0.3)',
              }}
            >
              <p className="text-sm font-manrope text-center" style={{ color: 'rgba(250, 204, 21, 0.95)' }}>
                 Can't find the email? <span className="font-semibold">Check your spam/junk folder!</span>
              </p>
            </div>
            
            <p className="text-xs font-manrope text-center" style={{ color: 'rgba(255,255,255,0.5)' }}>
              The code is valid for 10 minutes.
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}
