'use client'

import { motion } from 'framer-motion'
import { Card, CardContent } from './ui/card'
import { Plane, Users, Award, Zap } from 'lucide-react'
import { cn } from '../lib/utils'

const features = [
  {
    icon: Plane,
    title: 'Hands-On Building',
    description: 'Build both multirotor and fixed-wing drones from scratch',
    glowText: 'Practical Experience',
    glowColor: 'lime',
  },
  {
    icon: Users,
    title: 'Expert Mentorship',
    description: 'Learn from IISc UAV Lab professionals',
    glowText: 'Industry Leaders',
    glowColor: 'blue',
  },
  {
    icon: Award,
    title: 'Full Certification',
    description: 'Receive official certificate upon completion',
    glowText: 'Recognized Achievement',
    glowColor: 'purple',
  },
  {
    icon: Zap,
    title: 'Live Flight Testing',
    description: 'Test and fly your custom-built drones',
    glowText: 'Real Flight Time',
    glowColor: 'orange',
  },
]

const glowColors = {
  lime: {
    blur: 'bg-lime-400/90',
    shadow: 'shadow-[0_40px_80px_-16px_rgba(163,230,53,0.8)]',
    text: 'text-lime-500',
  },
  blue: {
    blur: 'bg-blue-400/90',
    shadow: 'shadow-[0_40px_80px_-16px_rgba(59,130,246,0.8)]',
    text: 'text-blue-500',
  },
  purple: {
    blur: 'bg-purple-400/90',
    shadow: 'shadow-[0_40px_80px_-16px_rgba(168,85,247,0.8)]',
    text: 'text-purple-500',
  },
  orange: {
    blur: 'bg-orange-400/90',
    shadow: 'shadow-[0_40px_80px_-16px_rgba(251,146,60,0.8)]',
    text: 'text-orange-500',
  },
}

export default function FeaturesSection() {
  return (
    <section className="relative py-20 px-4" style={{ background: 'var(--brand-black)' }}>
      <div className="max-w-7xl mx-auto">
        {/* Section Header */}
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-bold text-white mb-4">
            Why Do Drones?
          </h2>
          <p className="text-lg text-white/70 max-w-2xl mx-auto">
            Experience comprehensive drone education through our hands-on workshop
          </p>
        </div>

        {/* Features Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {features.map((feature, index) => {
            const Icon = feature.icon
            const colors = glowColors[feature.glowColor as keyof typeof glowColors]
            
            return (
              <motion.div
                key={feature.title}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.4, delay: index * 0.1 }}
                className="relative group"
              >
                {/* Glow effect below card */}
                <div 
                  className={cn(
                    "pointer-events-none absolute inset-x-3 -bottom-10 top-[90%] rounded-[28px] blur-xl z-0 transition-opacity duration-300 opacity-0 group-hover:opacity-100",
                    colors.blur,
                    colors.shadow
                  )} 
                />

                {/* Glow text below */}
                <div className="absolute inset-x-0 -bottom-10 mx-auto w-full z-0">
                  <div className="flex items-center justify-center gap-2 bg-transparent py-3 text-center text-sm font-semibold text-white transition-opacity duration-300 opacity-0 group-hover:opacity-100">
                    <Zap className="h-4 w-4" /> {feature.glowText}
                  </div>
                </div>

                {/* Glass card */}
                <Card className={cn(
                  "relative z-10 h-full overflow-visible rounded-[20px]",
                  "bg-white/10 dark:bg-white/5 backdrop-blur-xl",
                  "border border-white/20",
                  "shadow-lg hover:shadow-2xl",
                  "transition-all duration-300 hover:-translate-y-2"
                )}>
                  <CardContent className="p-6">
                    {/* Icon */}
                    <div className="mb-6 flex items-center justify-center">
                      <div className={cn(
                        "h-16 w-16 rounded-2xl flex items-center justify-center",
                        "bg-white/10 backdrop-blur-sm border border-white/20",
                        colors.text
                      )}>
                        <Icon className="h-8 w-8" />
                      </div>
                    </div>

                    {/* Content */}
                    <div className="text-center">
                      <h3 className="text-xl font-semibold text-white mb-2 tracking-tight">
                        {feature.title}
                      </h3>
                      <p className="text-sm text-white/70 leading-relaxed">
                        {feature.description}
                      </p>
                    </div>
                  </CardContent>
                </Card>
              </motion.div>
            )
          })}
        </div>
      </div>
    </section>
  )
}
