// src/App.js (or .jsx)
import React from 'react';
// import LandingPage from './components/LandingPage'; // Your old homepage component
// import FaqSection from './components/FaqSection';   // Your old FAQ component
import {HomePage} from './pages/Home'
import RootLayout from './pages/layout';
import { Route,Routes } from 'react-router-dom';
import FAQPage from './pages/FAQ';
import ProgramPageNew from './pages/programNew';
import ProgramPage from './pages/program';
import LoginPage from './pages/Login'
import VerifyOTPPage from './pages/LoginVerify'
import PortalPage from './pages/Portal'
import ProfilePage from './pages/PortalProfile'
import ConfirmPayPage from './pages/PortalRegistraion'
import RegistrationInactivePage from './pages/PortalRegistrationUnavaliable'
function pathToTag(path,tag){
  return <Route key={path} path ={path} element={RootLayout({children: tag})}/>
}
function App() {
  // Since you don't need routing, you can combine content here or 
  // just render your main landing component.
  return (
    <Routes>
      {[
        pathToTag('/',<HomePage/>),
        pathToTag('/program',<ProgramPage/>),
        // pathToTag('/programNew',<ProgramPageNew/>),
        pathToTag('/faq',<FAQPage/>),
        pathToTag('/portal',<PortalPage/>),
        pathToTag('/portal/profile',<ProfilePage/>),
        // pathToTag('/portal/very-secret-url/registration',<ConfirmPayPage/>),
        // pathToTag('/portal/registration',<RegistrationInactivePage/>),
        pathToTag('/portal/registration',<ConfirmPayPage/>),
        pathToTag('/login',<LoginPage/>),
        pathToTag('/login/verify',<VerifyOTPPage/>),
      ]}
    </Routes>
    // pathToTag('/',<Homepage/>)
  );
}

export default App;