// ============================================================================

type ConfirmPayState = 'needsProfile' | 'readyToPay' | 'success' | 'failure' | 'summary'

type ParticipantType = 'Student' | 'Faculty' | 'Working Professional'

interface ParticipantData {
  fullName: string
  phoneNumber: string
  email: string
  participantType: ParticipantType
  studentLevel?: 'UG' | 'PG' | 'PhD'
  graduationMonth?: string
  graduationYear?: string
  designation: string
  organization: string
}

interface PricingData {
  baseFee: number
  gst: number
  total: number
}

interface PaymentResult {
  success: boolean
  orderId?: string
  transactionId?: string
  amountPaid?: number
  participantNumber?: number
  uniqueId?: string
  merchPassCode?: string
  paymentDate?: string
}

