// ============================================================================
// HELPER FUNCTIONS
// ============================================================================

function calculatePricing(participantType: ParticipantType): PricingData {
  const fees = {
    'Student': 6000,
    'Faculty': 9000,
    'Working Professional': 12000
  }
  
  const baseFee = fees[participantType]
  const gst = Math.round(baseFee * 0.18)
  const total = baseFee + gst
  
  return { baseFee, gst, total }
}

function isEarlyBird(participantNumber: number, paymentDate: Date): boolean {
  // Early bird: participant number  40 AND before Nov 29, 2025 11:59 PM IST
  const cutoffDate = new Date('2025-11-29T23:59:59+05:30')
  return participantNumber <= 40 && paymentDate < cutoffDate
}

function generateDoDropsId(participantNumber: number): string {
  return `DDAE25${String(participantNumber).padStart(3, '0')}`
}

