"use client";

import { useState } from "react";
import { Badge } from "./badge";
import { Button } from "./button";
import { Popover, PopoverTrigger, PopoverContent } from "./popover";
import { Tabs, TabsList, TabsTrigger } from "./tabs";
import {
  Bell,
  GitMerge,
  FileText,
  ClipboardCheck,
  Mail,
  MessageSquareQuote,
  AlertCircle,
  LucideIcon,
} from "lucide-react";

interface Notification {
  id: number;
  user: string;
  action: string;
  target: string;
  timestamp: string;
  unread: boolean;
  icon: LucideIcon;
}

const initialNotifications: Notification[] = [
  // {
  //   id: 1,
  //   user: "Registration Team",
  //   action: "reminder",
  //   target: "Complete your profile to register",
  //   timestamp: "10 minutes ago",
  //   unread: true,
  //   icon: AlertCircle,
  // },
  // {
  //   id: 2,
  //   user: "Do Drones 2025",
  //   action: "shared file",
  //   target: "Event Schedule.pdf",
  //   timestamp: "30 minutes ago",
  //   unread: true,
  //   icon: FileText,
  // },
  // {
  //   id: 3,
  //   user: "Organizers",
  //   action: "assigned you a task",
  //   target: "Submit project proposal",
  //   timestamp: "2 hours ago",
  //   unread: false,
  //   icon: ClipboardCheck,
  // },
  // {
  //   id: 4,
  //   user: "Support Team",
  //   action: "sent you a message",
  //   target: "Registration confirmation",
  //   timestamp: "5 hours ago",
  //   unread: false,
  //   icon: Mail,
  // },
  // {
  //   id: 5,
  //   user: "Event Updates",
  //   action: "posted",
  //   target: "New workshop added: Drone Programming",
  //   timestamp: "1 day ago",
  //   unread: false,
  //   icon: MessageSquareQuote,
  // },
  // {
  //   id: 6,
  //   user: "System",
  //   action: "alert",
  //   target: "Early bird registration closing soon",
  //   timestamp: "3 days ago",
  //   unread: false,
  //   icon: AlertCircle,
  // },
];

function NotificationInboxPopover() {
  const [notifications, setNotifications] = useState(initialNotifications);
  const unreadCount = notifications.filter((n) => n.unread).length;
  const [tab, setTab] = useState("all");

  const filtered = tab === "unread" ? notifications.filter((n) => n.unread) : notifications;

  const markAsRead = (id: number) => {
    setNotifications(
      notifications.map((n) => (n.id === id ? { ...n, unread: false } : n)),
    );
  };

  const markAllAsRead = () => {
    setNotifications(notifications.map((n) => ({ ...n, unread: false })));
  };

  return (
    <Popover>
      <PopoverTrigger asChild>
        <button
          className="relative p-2 rounded-full hover:bg-white/5 transition text-white/70 hover:text-white"
          aria-label="Open notifications"
        >
          <Bell size={20} strokeWidth={2} aria-hidden="true" />
          {unreadCount > 0 && (
            <Badge className="absolute -top-1 -right-1 min-w-5 px-1 h-5 flex items-center justify-center bg-red-500 text-white text-xs">
              {unreadCount > 99 ? "99+" : unreadCount}
            </Badge>
          )}
        </button>
      </PopoverTrigger>
      <PopoverContent className="w-[380px] p-0 bg-black/95 backdrop-blur-xl border-white/20">
        {/* Header with Tabs + Mark All */}
        <Tabs value={tab} onValueChange={setTab}>
          <div className="flex items-center justify-between border-b border-white/10 px-3 py-2">
            <TabsList className="bg-transparent">
              <TabsTrigger value="all" className="text-sm text-white/70 data-[state=active]:text-white">All</TabsTrigger>
              <TabsTrigger value="unread" className="text-sm text-white/70 data-[state=active]:text-white">
                Unread {unreadCount > 0 && <Badge className="ml-2 px-2 min-w-[24px] h-5 flex items-center justify-center bg-red-500 text-white text-xs">{unreadCount}</Badge>}
              </TabsTrigger>
            </TabsList>
            {unreadCount > 0 && (
              <button
                onClick={markAllAsRead}
                className="text-xs font-medium text-white/60 hover:text-white hover:underline"
              >
                Mark all as read
              </button>
            )}
          </div>

          {/* Notifications List */}
          <div className="max-h-80 overflow-y-auto">
            {filtered.length === 0 ? (
              <div className="px-3 py-6 text-center text-sm text-white/60">
                No notifications
              </div>
            ) : (
              filtered.map((n) => {
                const Icon = n.icon;
                return (
                  <button
                    key={n.id}
                    onClick={() => markAsRead(n.id)}
                    className="flex w-full items-start gap-3 border-b border-white/10 px-3 py-3 text-left hover:bg-white/5 transition"
                  >
                    <div className="mt-1 text-white/60">
                      <Icon size={18} />
                    </div>
                    <div className="flex-1 space-y-1">
                      <p
                        className={`text-sm ${
                          n.unread ? "font-semibold text-white" : "text-white/70"
                        }`}
                      >
                        {n.user} {n.action}{" "}
                        <span className="font-medium">{n.target}</span>
                      </p>
                      <p className="text-xs text-white/50">{n.timestamp}</p>
                    </div>
                    {n.unread && (
                      <span className="mt-1 inline-block size-2 rounded-full bg-blue-500" />
                    )}
                  </button>
                );
              })
            )}
          </div>
        </Tabs>

        {/* Footer */}
        <div className="px-3 py-2 text-center border-t border-white/10">
          <Button variant="ghost" size="sm" className="w-full text-white/70 hover:text-white hover:bg-white/5">
            View all notifications
          </Button>
        </div>
      </PopoverContent>
    </Popover>
  );
}

export { NotificationInboxPopover };
