'use client'

import React, { useEffect, useState } from 'react'
import HomeHero from '../components/HomeHero'
import Footer from '../components/Footer'
import SponsorsMarquee from '../components/SponsorsMarquee'
import FeaturesSection from '../components/FeaturesSection'
import { Link } from 'react-router-dom'
import BrandTitle from '../components/BrandTitle'
import { InteractiveHoverButton } from '../components/ui/interactive-hover-button'
import { siteConfig } from '../lib/siteConfig'
import { useNavigate } from 'react-router-dom'
import { cn } from '../lib/utils'

export async function checkFlaskAuth() {
  const getCookie = (name:string) => {
    return document.cookie
      .split('; ')
      .find((row) => row.startsWith(`${name}=`));
  };
  // return true;
  const sessionCookie = getCookie('sessionExists');
  if (!sessionCookie) {
    // console.log("No session cookie found. Skipping API check.");
    return false; 
  }
  // 2. Server-Side Verification
  try {
    const response = await fetch('/api/check-auth', {
      method: 'GET',
      credentials: 'include', 
      headers: {
        'Content-Type': 'application/json',
      }
    });
    if (response.ok) {
      const data = await response.json();
      return data.isAuthenticated; // Assuming data is the boolean itself, or data.isAuthenticated
    }
    return false;
  } catch (error) {
    console.error('Auth Check Error:', error);
    return false;
  }
}
export function HomePage() {
  const whatsappUrl = siteConfig.urls.whatsappChannel
  const navigate=useNavigate()
  const [mobileMenuOpen, setMobileMenuOpen] = React.useState(false)
  const [authChecking, setAuthChecking] = useState(true)
  
  // Check auth and redirect if logged in
  useEffect(() => {
    const verifyAuth = async () => {
      const isAuthenticated = await checkFlaskAuth();
      if (isAuthenticated) {
        window.location.href='/portal';
      } else {
        setAuthChecking(false);
      }
    };
    verifyAuth();
  }, [navigate]); 
  
  const handleLoginClick = () => {
    navigate('/login');
  };
  
  const handleJoinChannelClick = () => {
    window.open(whatsappUrl, '_blank', 'noopener,noreferrer');
  };
  
  // Show loading or nothing while checking auth
  if (authChecking) {
    return null
  }
  
  return (
    <div className="flex min-h-screen flex-col" style={{ background: 'radial-gradient(ellipse at center top, rgba(15,23,42,0.9) 0%, rgba(7,7,7,1) 50%)', backgroundColor: 'var(--brand-black)' }}>
      {/* Old header removed - Using TubelightNavBar from layout instead */}
      
      {/* Main content with MD3-style vertical rhythm */}
      <main className="flex-1 flex flex-col">
        {/* Sponsors marquee section - clear spacing from header */}
        <div className="pt-8 pb-6 md:pb-8 px-4 sm:px-6 lg:px-10">
          <SponsorsMarquee />
        </div>
        
        {/* Hero section - medium gap from marquee */}
        <div className="pb-12 md:pb-16">
          <HomeHero />
        </div>
        
        {/* Features section with glassmorphism cards */}
        <div className="pb-12 md:pb-16">
          <FeaturesSection />
        </div>
      </main>
      
      {/* Footer - clear separation with large margin */}
      <Footer />
    </div>
  )
}
